.paperWrapper {
  border-radius: 5px;
  box-shadow: 1px 1px;
  color: gray;
  margin: 2% 10% 1% 10%;
  padding: 4%;
  display: flex;
  flex-direction: column;
  background: white;
}

.topContainer {
  display: flex;
  flex-direction: column;
}

.bottomContainer {
  display: flex;
  flex-direction: row; 
}
.blockLeft {
  width: 60%;
  display: flex;
  flex-direction: column;
  padding-right: 2%;
}
.blockRight {
  width: 40%;
  display: flex;
  flex-direction: column;
}

.header {
  display: flex;
  flex-direction: row;
}
.fullname {
  width: 80%;
  color: black;
  font-family: 'Courier New', Courier, monospace;
}
.rightOfFullname {
  width: 20%;
}
.briefIntro {
  color: black;
  font-family: 'Courier New', Courier, monospace;
  font-size: 1.2em;
}
.link {
  color: #ec407a;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  text-decoration: none;
}
.itemContainer {
  color: black;
  margin: 10px 0px;
}
.itemTitle {
  text-transform: uppercase;
  margin: 10px 2px;
  font-size: 16px;
  font-weight: 500;
}
.itemContent {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 5px 2px;
}
.itemTwoColumn {
  display: flex;
  flex-direction: row;
  font-size: 15px;
}
.itemLeftContent {
  width: 30%;
  max-width: 30%;
  min-width: 30%;
  color: gray;
  font-weight: 600;
}
.itemRightContent {
  width: 70%;
  max-width: 70%;
  min-width: 70%;
  font-weight: 500;
}
.skillContainer {
  display: flex;
  flex-wrap: wrap;
  /* flex-direction: column; */
}
.skillItem {
  max-width: 50%;
  width: 50%;
  display: flex;
  flex-direction: row;
  margin: 5px 0px;
}
.skillImage {
  width: 40px;
  height: 40px;
  margin: 5px;
}
.skillName {
  margin-left: 5px;
}
.education {
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  margin: 10px 0;
}
.educationLocation {
  color: gray;
  font-size: 1.2em;
}
/* .educationLocation:hover {
  font-size: 1.2em;
} */
.educationTime {
  color: gray;
  margin: 2px 0px;
  font-size: 0.8em;
}
.experienceTitle {
  color: black;
  font-size: 20px;
}
/* .experienceTitle:hover {
  color: #303030;
  font-size: 19.5px;
} */

@media only screen and (max-width: 600px) {
  .paperWrapper {
    margin: 2% 1%;
    padding: 1%;
  }

  .topContainer {
    display: flex;
    flex-direction: column;
  }

  .bottomContainer {
    flex-direction: column; 
  }
  .blockLeft {
    width: 100%;
  }
  .blockRight {
    width: 100%;
  }
  .itemTitle {
    text-transform: uppercase;
    margin: 10px 2px;
    font-size: 20px;
    font-weight: 500;
  }
  .itemContent {
    margin: 5px 10px;
  }
  .itemTwoColumn {
    margin: 5px 10px;
  }
  .skillContainer {
    display: flex;
    flex-wrap: wrap;
    /* flex-direction: column; */
  }
}