@import url('https://fonts.googleapis.com/css?family=Cabin|Cabin+Condensed|Cabin+Sketch|Inconsolata|Lobster|Lobster+Two|Roboto');

.pageContainer {
  font-family: 'Inconsolata', monospace;
  min-height: 70vh;
  padding: 2%;
}

.blogList {
  display: flex;
  flex-direction: column;
  background: white;
  /* margin: 0px 5%; */
  border-radius: 0px 0px 5px 5px;
  box-lines: 5px;
  flex: 1;
  max-height: 200px;
  overflow: auto;
  font-size: 1em;
}
.blogItem {
  border-top: gray solid 1px;
  padding: 15px 10px;
  text-align: center;
  box-sizing: border-box;
}
.twoColumnContainer {
  display: flex;
  flex-direction: row;
}
.leftColumnContainer {
  display: flex;
  flex-direction: column;
  width: 25%; 
  max-width: 25%; 
  min-width: 25%; 
  /* border: blue solid 2px; */
  margin-right: 5%;
  /* height: 200px;
  overflow: auto; */
  font-family: 'Inconsolata', monospace;
}
.rightColumnContainer {
  display: flex;
  width: 65%;
  position: relative;
  /* border: red solid 2px; */
  margin-left: 5%;
}
.blogContent {
  /* background: lightblue; */
  background: white;
  padding: 5vw 10px 10px 5vw;
  /* margin: 0% 5%; */
  width: auto;
  overflow: auto;
  flex: 1;
}
/* .blogItem:after {
  content: "";
  position: absolute;
  background: gray;
  height: 1px;
  width: 60%;
  left: 20%;
} */
.blogItem .link {
  color: #ec407a;
  text-decoration: none;
  font-weight: 700;
}
.blogItem .link:hover {
  color: purple;
}
@media only screen and (max-width: 600px) {
  .pageContainer {
    padding: 0;
  }
  .twoColumnContainer {
    flex-direction: column;
  }
  .leftColumnContainer {
    width: auto;
    max-width: unset;
    min-width: unset;
    margin-right: unset;
    margin: 10px;
  }
  .blogList {
    position: relative;
    width: auto;
    height: 100px;
  }
  .rightColumnContainer {
    width: auto;
    margin-left: unset;
    margin: 10px;
  }
}